.flex {
    display: flex;
    box-sizing: border-box;
}

.grid {
    display: grid;
}

.absolute {
    box-sizing: content-box;
    position: absolute;
}

.full-height {height: 100vh;}

.row {flex-direction: row;}
.column {flex-direction: column;}

.la {justify-content: flex-start;}
.ca {justify-content: center;}
.ra {justify-content: flex-end;}
.sb {justify-content: space-between;}
.sa {justify-content: space-around;}

.vla {align-items: flex-start;}
.vca {align-items: center;}
.vra {align-items: flex-end;}
.vsb {align-items: space-between;}
.vsa {align-items: space-around;}

.grow {flex-grow: 1;}
.shrink {flex-shrink: 1;}

