.tab{
      border-radius: 8px 8px 0px 0px;
      padding: 10px 10px 6px 10px;
    }

  .no-overflow{
      overflow: hidden;
  }

  .no-margin {
      margin: 0;
  }
  
  .drag-to-safety {
    min-height: 30px;
  }

  .dropdown-content {
    animation: navAnim .2s ease-in-out;
    transform-origin: top;
  }

  .dropdown.is-right .dropdown-menu {
    left: 0;
  }

  .prevent-overflow {
    overflow-wrap: break-word;
    word-wrap: break-word;
    word-break: break-word;
    -ms-hyphens: auto;
    -moz-hyphens: auto;
    -webkit-hyphens: auto;
    hyphens: auto;
  }

  @keyframes navAnim {
    0% {
      display: none;
      transform: scaleY(0);
      opacity: 0;
    }
    1% {
      display: block;
      transform: scaleY(0);
      opacity: 0;
    }
    30% {
      opacity: 0;
    }
    100% {
      transform: scaleY(1);
      opacity: 1;
    }
  }

  .required::after {
    content: '*';
    color: #B22222;
  }

  .draggable {
    cursor: move;
  }
  
  .list-item {
    display: inline-block;
    margin-right: 10px;
  }
  .list-enter-active,
  .list-leave-active {
    transition: all 0.2s ease;
  }
  .list-enter-from,
  .list-leave-to {
    opacity: 0;
  }

  .fit-width {
    width: fit-content;
  }

  .custom-html {
    warning {
      color: #B22222;
    }
    img {
      display: block;
      margin: 0 auto;
    }

    hr {
      border: none;
      border-top: 2px solid rgba(#0D0D0D, 0.1);
      margin: 2rem 0;
    }
    p:after {content: "\00a0\00a0"}

    .ProseMirror p:after {
      content : ""
    }

    clickable {
      -webkit-font-smoothing: antialiased;
      text-size-adjust: 100%;
      outline: none;
      box-sizing: inherit;
      margin: 0;
      -webkit-appearance: none;
      align-items: center;
      border: 1px solid transparent;
      border-radius: 4px;
      box-shadow: none;
      display: inline-flex;
      font-size: 1rem;
      height: 2.5em;
      line-height: 1.5;
      position: relative;
      vertical-align: top;
      user-select: none;
      border-width: 1px;
      cursor: pointer;
      justify-content: center;
      padding-bottom: calc(0.5em - 1px);
      padding-left: 1em;
      padding-right: 1em;
      padding-top: calc(0.5em - 1px);
      text-align: center;
      white-space: nowrap;
      background-color: #3273dc;
      border-color: transparent;
      color: #fff;

      &:hover {
        background-color: #276cda;
      }

      &:focus {
        background-color: #3273dc;
        box-shadow: 0 0 0 0.125em rgba(50, 115, 220, 0.25);
      }
    }
  }

  .snackbar {
    width: 100vw;
    position: fixed;
    left: 0;
    z-index: 100;
    pointer-events: none;
  }
  .top {
      top: 30px;
    }

  .bottom {
    bottom: 30px;
  }

  .ontop {
     z-index: 101;
  }

  .snackbar>* {
    pointer-events: auto;
  }

  .section-first {
    border-left: 1px solid #dbdbdb !important;
  }
  .section-last {
    border-right: 1px solid #dbdbdb !important;
  }
  .mt-1px {
    margin-top: 1px;
  }

  .connection-error>* {
    background-color: #f14668 !important;
    color: white !important;
  }

  .connection-error option {
    background-color: white !important;
    color: black !important;
  }

  .invalidator {
    caret-color: transparent; 
    pointer-events:none;
  }

  .removable {
    -webkit-transition: opacity 100ms linear;
    transition: opacity 100ms linear;
  }
  .hidden {
    opacity: 0;
  }

  @keyframes slideInFromLeft {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

  @media only screen and (min-width: 768px) {
    .max-char {
      margin-top: -24px;
      justify-content: flex-end;
    }
  }
  .slide-fade-enter-active,
  .slide-fade-bottom-enter-active {
    transition: all 0.3s ease-out;
  }

  .slide-fade-leave-active,
  .slide-fade-bottom-leave-active {
    transition: all 0.5s ease-out;
  }

  .slide-fade-enter-from,
  .slide-fade-leave-to {
    transform: translateY(-40px);
    opacity: 0;
  }

  .slide-fade-bottom-enter-from,
  .slide-fade-bottom-leave-to {
    transform: translateY(40px);
    opacity: 0;
  }