<template>
  <div class="flex column vca">
    <FormInvalidator 
      :valid="valid"
      :start-invalid="triedSubmit"
      :invalid-string="'A data e hora final deve ser após a data e hora inicial.'"
      @invalid="$emit('uncollapse'); triedSubmit = true;"
    />
    <Dropdown
      :text="'Opções avançadas'"
      :valid="!(triedSubmit && !valid)"
    >
      <div class="control dropdown-item">
        <label
          class="label"
          :for="qid+'_default'"
        >Valor por defeito: </label>
        <Input
          :id="qid+'_default'"
          v-model="result.default"
          class="input is-small"
          type="datetime-local"
          @input="update"
        />
      </div>
      <div class="control dropdown-item">
        <label
          class="label"
          :for="qid+'_begin'"
        >Data e hora mínima: </label>
        <Input
          :id="qid+'_begin'"
          v-model="result.begin"
          class="input is-small"
          type="datetime-local"
          @input="update"
        />
      </div>
      <div class="control dropdown-item">
        <label
          class="label"
          :for="qid+'_end'"
        >Data e hora máxima: </label>
        <Input
          :id="qid+'_end'"
          v-model="result.end"
          class="input is-small"
          type="datetime-local"
          @input="update"
        />
      </div>
    </Dropdown>
  </div>
</template>

<script>

import Dropdown from "@/components/Dropdown";
import FormInvalidator from "@/components/FormInvalidator";
import Input from "@/components/Input"
export default {
  name: "DateField",
  props: ["qid", "properties"],
  emits: ["update", "uncollapse"],
  components: {Dropdown, FormInvalidator, Input},
  data() {
    return {
      result: this.properties,
      valid: true,
      triedSubmit : false,
      invalidString : ""
    };
  },
  methods: {
    update() {
      this.valid = !this.result.begin || !this.result.end || this.result.begin <= this.result.end
      if (!this.valid)
        this.invalidString = "A data final deve ser após a data inicial.";
      else {
        this.valid = this.$refs['begin'].validity_valid && this.$refs['end'].validity_valid;
        this.invalidString = " ";
      }
      if (this.result.default === "")
        this.result.default = undefined;
      if (this.result.begin === "")
        this.result.begin = undefined;
      if (this.result.end === "")
        this.result.end = undefined;
      this.$emit("update", this.result);
    },
  },
};
</script>

<style type="scss">
  .option-label {
    display: inline-block;
    width: 145px;
    white-space: nowrap;
  }
</style>
