<template>
  <div>
    <div class="level is-flex-grow-1 is-align-items-flex-start">
      <div class="level-left is-flex-grow-1 is-justify-content-center">
        <div class="columns is-centered is-mobile is-flex-grow-1">
          <div class="column level-item is-flex-grow-2">
            <draggable
              v-model="result.options"
              tag="transition-group"
              
              :component-data="{
                name: 'list',
                tag: 'div'}"
              :move="move()"
              v-bind="dragOptions"
              handle=".draggable"
              item-key="id"
              class="is-flex-grow-1"
              @end="update()"
            >
              <template #item="{element, index}">
                <div class="field has-addons is-rounded multiplechoice-field is-flex-grow-1">
                  <div
                    class="control"
                    :class="{'draggable' : result.options.length > 1}"
                  >
                    <span
                      v-show="result.options.length > 1"
                      class="button is-static "
                      :class="result.default == element.id ? 'is-primary is-light' : ''"
                    >
                      <span class="icon">
                        <i class="fas fa-ellipsis-v" />
                      </span>
                    </span>
                    <span
                      v-show="result.options.length <= 1"
                      class="button is-static"
                      :class="result.default ? 'is-primary is-light' : ''"
                    >
                      <span class="icon">
                        <i class="fas fa-ban" />
                      </span>
                    </span>
                  </div>
                  <div class="control is-expanded">
                    <Input
                      :id="qid+'_option_'+index"
                      v-model="element.question"
                      type="text"
                      required
                      class="input is-expanded"
                      placeholder="Texto da opção"
                      @input="update"
                    />
                  </div>
                  <div
                    class="control"
                    @click="deleteOption(index)"
                  >
                    <button
                      type="button"
                      class="button is-danger"
                      :disabled="result.options.length <= 1"
                    >
                      <span class="icon">
                        <i class="fas fa-trash-alt" />
                      </span>
                    </button>
                  </div>
                </div>
              </template>
            </draggable>
            <div
              class="component level-item mt-3"
            >
              <button
                type="button"
                class="button round is-primary is-light"
                @click="result.options.push(newElement()); update();"
              >
                <span
                  class="icon"
                  aria-label="Adicionar Opção"
                >
                  <i class="fas fa-plus" />
                </span>
              </button>
            </div>
          </div>
        </div>
      </div>
      <div class="level-right is-flex-grow-1 level is-mobile">
        <div class="is-mobile is-flex-grow-1 flex column la vca">
          <FormInvalidator 
            :valid="valid"
            :invalid-string="invalidString"
            :start-invalid="triedSubmit"
            @invalid="$emit('uncollapse'); triedSubmit = true;"
          />
          <Dropdown
            :text="'Opções avançadas'"
            :valid="!(triedSubmit && !valid)"
          >
            <div class="dropdown-item">
              <label
                class="label"
                :for="qid + '_style'"
              >Estilo: </label>
              <div class="control has-icons-left">
                <div class="select">
                  <select
                    :id="qid + '_style'"
                    v-model="result.style"
                    @change="update()"
                  >
                    <option value="radio">
                      Radio
                    </option>
                    <option
                      value="dropdown"
                      :disabled="result.allowed_other"
                    >
                      Dropdown <template v-if="result.allowed_other">
                        (Não suporta opção "outro")
                      </template>
                    </option>
                  </select>
                </div>
                <span
                  v-show="result.style == 'radio'"
                  class="icon is-left"
                >
                  <i class="far fa-dot-circle" />
                </span>
                <span
                  v-show="result.style == 'dropdown'"
                  class="icon is-left"
                >
                  <i class="far fa-caret-square-down" />
                </span>
              </div>
            </div>
            <div class="control dropdown-item">
              <span class="label">Selecionado por defeito: </span>
              <label
                class="radio prevent-overflow is-small"
                :for="qid+'_default_none'"
              >
                <input 
                  :id="qid+'_default_none'"
                  v-model="result.default"
                  :value="undefined"
                  class="mr-1"
                  type="radio"
                  @change="update"
                ><b>Nenhum</b></label>
              <span
                v-for="option in result.options"
                :key="option.id"
              >
                <br>
                <label
                  class="radio prevent-overflow is-small"
                  :for="qid+'_default_'+option.id"
                >
                  <input 
                    :id="qid+'_default_'+option.id"
                    v-model="result.default"
                    :value="option.id"
                    class="mr-1"
                    type="radio"
                    @change="update"
                  >{{ option.question }}<i v-if="option.question == ''">Opção sem texto</i></label></span>
            </div>
            <template v-if="result.style !== 'dropdown'">
              <hr class="dropdown-divider">
              <div class="control dropdown-item">
                <Switch
                  v-model="result.allowed_other"
                  :text="'Permitir a resposta \'outro\''"
                  @update:modelValue="update()"
                />
              </div>
            </template>
                    
            <div v-show="result.allowed_other">
              <hr class="dropdown-divider">
              <div class="control dropdown-item">
                <label
                  class="label"
                  :for="qid+'_other_string'"
                >Pergunta de outro: </label>
                <Input
                  :id="qid+'_other_string'"
                  v-model="result.other_string"
                  class="input is-small"
                  placeholder="Exemplo: 'Outra opção:' "
                  type="text"
                  @input="update"
                />
              </div>
              <div class="control dropdown-item">
                <label
                  class="label"
                  :for="qid+'_placeholder'"
                >Texto de sugestão (outro): </label>
                <Input
                  :id="qid+'_placeholder'"
                  v-model="result.placeholder"
                  placeholder="Texto de sugestão, tal como este"
                  class="input is-small"
                  type="text"
                  @input="update"
                />
              </div>
              <div class="control dropdown-item">
                <label
                  class="label"
                  :for="qid+'_min_length'"
                >Tamanho mínimo (outro): </label>
                <Input
                  :id="qid+'_min_length'"
                  ref="min_length"
                  v-model.number="result.min_length"
                  placeholder="Deve ser inferior ou igual ao máximo"
                  class="input is-small"
                  type="number"
                  step="1"
                  min="1"
                  @input="update"
                />
              </div>
              <div class="control dropdown-item">
                <label
                  class="label"
                  :for="qid+'_max_length'"
                >Tamanho máximo (outro): </label>
                <Input
                  :id="qid+'_max_length'"
                  ref="max_length"
                  v-model.number="result.max_length"
                  class="input is-small"
                  placeholder="Deve ser superior ou igual ao mínimo"
                  type="number"
                  step="1"
                  min="1"
                  @input="update"
                />
              </div>
            </div>
          </Dropdown>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Switch from "@/components/Switch";
import FormInvalidator from "@/components/FormInvalidator";
import Dropdown from "@/components/Dropdown";
import draggable from "vuedraggable";
import Input from "@/components/Input"

export default {
  name: "MultipleChoiceField",
  components : {draggable, Switch, FormInvalidator, Dropdown, Input},
  
  props: ["qid", "properties"],
  emits: ["update", "uncollapse"],
  data() {
    return {
      result: this.properties,
      valid: true,
      invalidString: "",
      triedSubmit: false
    };
  },
  created() {
    if (this.result.options === undefined)
      this.result.options = [this.newElement()];
    if (this.result.style === undefined)
      this.result.style = "radio";
  },
  methods: {
    move() {
      return (event) => {
        return (event.related.classList.contains("multiplechoice-field"))
      }
    },
    deleteOption(index) {
      if (this.result.options.length > 1) {
        this.result.options.splice(index, 1);
        this.update();
      }
    },
    newElement() {
      return {'id' : this.getUid(), 'question' : ''}
    },
    getUid() {
      return this.$store.getters.getUid;
    },
    update() {
      this.valid = !this.result.min_length || !this.result.max_length || this.result.min_length <= this.result.max_length
      if (!this.valid)
        this.invalidString = "O tamanho mínimo da resposta de \"outro\" deve ser inferior ou igual ao tamanho máximo.";
      else if (this.result.allowed_other) {
        this.valid = this.$refs['min_length'].validity_valid && this.$refs['max_length'].validity_valid;
        this.invalidString = "";
      }
      if (!this.result.allowed_other)
        this.result.allowed_other = undefined;
      if (this.result.placeholder === "" || !this.result.allowed_other)
        this.result.placeholder = undefined;
      if (this.result.min_length === "" || !this.result.allowed_other)
        this.result.min_length = undefined;
      if (this.result.max_length === "" || !this.result.allowed_other)
        this.result.max_length = undefined;
      if (this.result.other_string === "" || !this.result.allowed_other)
        this.result.other_string = undefined;
      this.$emit("update", this.result);
    },
  },
  computed: {
    dragOptions() {
      return {
        animation: 200,
        group: "description",
        disabled: false,
        ghostClass: "ghost"
      };
    }
  }
};
</script>

<style type="scss" scoped>
  .list-item {
    display: inline-block;
    margin-right: 10px;
  }
  .list-enter-active,
  .list-leave-active {
    transition: all 0.2s ease;
  }
  .list-enter-from,
  .list-leave-to {
    opacity: 0;
  }

  .fit-width {
    width: fit-content;
  }
</style>
